@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

body, html { height: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) { * { -webkit-text-size-adjust: none; } }
table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@font-face { font-family: "svgfont"; src: url("fonts/svgfont.eot"); src: url("fonts/svgfont.eot?#iefix") format("eot"), url("fonts/svgfont.woff") format("woff"), url("fonts/svgfont.woff2") format("woff2"), url("fonts/svgfont.ttf") format("truetype"); font-weight: normal; font-style: normal; }
.icon, [class^="icon-"], [class*=" icon-"] { font-family: "svgfont"; font-style: normal; font-weight: normal; text-rendering: auto; speak: none; line-height: 1; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.icon, [class^="icon-"], [class*=" icon-"] { display: inline-block; }

.icon-sepa:before { content: ""; }

.icon-skrill:before { content: ""; }

body { font-family: Roboto, sans-serif; font-size: 12px; line-height: 1.4; }

a { color: #0049a4; }

a:visited { color: #aa0bcc; }

.out { max-width: 1400px; margin: 0 auto; padding-bottom: 14px; }
@media only screen and (max-width: 1480px) { .out { margin: 0 40px; } }
@media only screen and (max-width: 800px) { .out { margin: 0 20px; } }

.totop { font-size: 13px; color: #094ca1; text-decoration: none; margin-top: 108px; display: block; }
.totop:visited { color: #094ca1; }
@media only screen and (max-width: 600px) { .totop { margin-top: 40px; } }
.totop i { background-image: url("../img/icons.png"); width: 11px; height: 19px; background-position: 0px -18px; display: inline-block; margin-right: 6px; }

.footer { border-top: 1px solid #dadcdd; border-bottom: 1px solid #dadcdd; padding: 11px 0; margin-top: 30px; *zoom: 1; }
.footer:after { content: " "; display: table; clear: both; }
.footer .footer__copy { float: left; font-size: 13px; color: #000000; }
@media only screen and (max-width: 750px) { .footer .footer__copy { text-align: left; float: none; margin-bottom: 5px; } }
.footer a { float: right; text-decoration: none; font-size: 13px; color: #000; position: relative; margin-left: 60px; display: block; }
.footer a:visited { color: #000; }
@media only screen and (max-width: 750px) { .footer a { float: none; margin: 0 0 5px 20px; } }
.footer a:before { content: ""; position: absolute; left: -20px; top: 0; background-image: url("../img/icons.png"); width: 14px; height: 12px; background-position: -15px -18px; }
.footer .footer__skype { margin-left: 70px; }
@media only screen and (max-width: 750px) { .footer .footer__skype { margin-left: 20px; } }
.footer .footer__skype:before { background-image: url("../img/icons.png"); width: 14px; height: 12px; background-position: -15px -18px; left: -18px; top: 2px; }
.footer .footer__phone:before { background-image: url("../img/icons.png"); width: 10px; height: 16px; background-position: -53px 0px; left: -18px; top: 1px; }
@media only screen and (max-width: 750px) { .footer .footer__email { margin-left: 22px; } }
.footer .footer__email:before { background-image: url("../img/icons.png"); width: 14px; height: 10px; background-position: -33px -18px; top: 3px; left: -21px; }

.work { font-size: 15px; }
.work.traditing .work__col { max-width: 762px; text-align: center; }
.work.traditing .work__banner { text-align: center; padding-top: 150px; }
.work__text { max-width: 605px; }
.work.work_alphabet .work__pic { margin-top: 48px; }
.work.work_alphabet .units { padding-top: 0; }
.work.work_alphabet .unit { padding: 0; margin-bottom: 18px; display: block; width: auto; }
.work.work_alphabet .unit:last-child { margin-bottom: 0; }
.work.work_alphabet .unit img { width: auto; }
.work h1 { font-size: 30px; font-weight: bold; margin-bottom: 6px; }
.work figure { margin-top: 42px; }
.work figure img { max-width: 100%; display: block; }
.work .e-art { padding-bottom: 10px; }
.work .shadow { box-shadow: 0 10px 24px 0px rgba(0, 0, 0, 0.1), inset 0 -11px 10px 0 rgba(0, 0, 0, 0.1); border-radius: 10px; display: inline-block; overflow: hidden; }
.work .shadow.is-mod { box-shadow: 0 10px 15px 0px rgba(0, 0, 0, 0.4); }
.work .shadow_m { margin: 0 -12px; }
@media only screen and (max-width: 900px) { .work .shadow_m { margin: auto; } }

.header { padding-top: 25px; position: relative; margin-bottom: 94px; }
@media only screen and (max-width: 600px) { .header { margin-bottom: 40px; } }
.header .header__title { font-size: 22px; font-weight: 500; color: #000; text-decoration: none; display: inline-block; margin-right: 170px; vertical-align: baseline; }
.header a.header__title { color: #094ca1; }
.header .header__lang { position: absolute; bottom: 2px; right: 0; font-size: 13px; color: #bdbdbd; }
.header .header__lang a { font-size: 13px; color: #bdbdbd; text-decoration: none; margin: 0 3px; }
.header nav { font-size: 0; display: inline-block; vertical-align: baseline; }
@media only screen and (max-width: 600px) { .header nav { display: block; margin-top: 10px; } }
.header nav a, .header nav i { text-decoration: none; font-style: normal; color: #094ca1; font-size: 14px; }
.header nav a:first-child, .header nav i:first-child { margin-right: 40px; }
.header nav i:hover, .header nav i { text-decoration: none; color: #000; }
.header nav .nav__separator { font-size: 14px; color: #094ca1; margin: 0 3px; }

.content { *zoom: 1; }
.content:after { content: " "; display: table; clear: both; }

.content__left { width: 46%; float: left; margin-right: 12%; }
@media only screen and (max-width: 700px) { .content__left { width: auto; float: none; } }

.content__right { float: left; width: 210px; }
@media only screen and (max-width: 700px) { .content__right { width: auto; float: none; margin-top: 50px; } }

.about { font-size: 14px; line-height: 21px; text-align: left; }
.about a { text-decoration: none; color: #000; }
.about a:hover { text-decoration: underline; }
.about__photo { margin-bottom: 18px; }
.about .about__leading { margin-bottom: 38px; line-height: 24px; font-size: 16px; }
.about .about__leading a { font-weight: 500; color: #084798; text-decoration: none; display: block; }

.homepage { margin-top: -20px; padding-bottom: 58px; }
@media only screen and (max-width: 600px) { .homepage { padding-bottom: 20px; } }
.homepage h1 { font-size: 36px; line-height: 42px; font-weight: bold; color: #111111; margin-bottom: 10px; }
.homepage p { font-size: 18px; font-style: italic; line-height: 24px; margin-bottom: 45px; }
.homepage p.is-mod { font-style: normal; }

.list { font-size: 0; max-width: 1400px; }
@media only screen and (max-width: 600px) { .list { text-align: center; } }

.item { display: inline-block; width: 200px; margin-right: 55px; text-align: center; text-decoration: none; margin-bottom: 60px; }
@media only screen and (max-width: 600px) { .item { margin-bottom: 20px; } }
.item .item__pic { margin-bottom: 15px; }
.item .item__title { color: #094ca1; text-decoration: none; font-size: 14px; margin-bottom: 3px; }
.item .item__type { font-size: 12px; color: #bdbdbd; font-style: italic; }
@media only screen and (max-width: 600px) { .item { margin-right: 0; } }

.text { font-size: 16px; color: #000000; line-height: 24px; margin-top: -10px; }
.text a { color: #084798; text-decoration: none; }
.text p { margin-bottom: 1.5em; }
.text h2 { margin-top: 76px; margin-bottom: 5px; }
.text h1 { font-size: 36px; margin-bottom: 52px; line-height: 42px; font-weight: bold; color: #111111; }

.units { position: relative; padding-top: 42px; }
.units__wrp { margin: 0 -10px; font-size: 0; }
.units .unit { width: 50%; padding: 5px; margin: 0; display: inline-block; }
.units .unit img { height: auto; width: 100%; }


